.section-top__image{
  width: 100%;
}

.blockquote{
  font-family: $ubuntu;
}

.section-top__ul{
  padding: 0;
  margin: 0;
  list-style: none;
  @media (min-width:500px){
    display:flex;
    justify-content: space-between;

  }
}
.section-top__side{
  flex-basis: 48%;
  text-align: left;
}
.section-top__ul-item{
  letter-spacing: 1px;
  font-weight: bold;
  font-size: $font-m;
  margin-bottom: .5rem;
  @media (min-width:990px) {
    font-size: $font-l;

  }
}

.section-top__icon{
  padding: 0 .5rem;
  color: $color_ytb;
}
